import React from "react";
import { ICustomerStory } from '../../interfaces';
import "./CustomerStory.sass"
import Img from 'gatsby-image';
import { Link } from "gatsby";
import { FaArrowRight, FaLongArrowAltRight } from "react-icons/fa";
import { OutboundLink } from "gatsby-plugin-google-analytics";

export class HomeCustomerStory extends React.Component<{stories: ICustomerStory[]}>
{
  render()
  {
    return (
      <div>
        <div className="home-customerstory">
          {
            this.props.stories.map((s,i) => (
              <div key={i} className={"customerstory-container " + (i%2 == 0 ? "story-1" : "story-2")}>
                <div className="customerstory-icon">
                  <OutboundLink href={s.fields.story.url} rel="nofollow" className="customerstory-company" target="_blank">
                    <Img fixed={s.childImageSharp.fixed} className={s.fields.story.className}></Img>
                  </OutboundLink>
                </div>
                <div className="customerstory-text">
                  <span>
                    <h3><OutboundLink href={s.fields.story.url} rel="nofollow" className="customerstory-company" target="_blank">{s.fields.story.company}</OutboundLink></h3>
                    <p>{s.fields.story.text}</p>
                  </span>
                  <span>
                    <span className="customerstory-author">{s.fields.story.author}</span>
                    <span className="customerstory-position"> ({s.fields.story.position}</span>
                    {
                      s.fields.story.country != null ? <span className="customerstory-country">, {s.fields.story.country}</span> : ""
                    }
                    <span className="customerstory-author">)</span>
                  </span>
                </div>
              </div>
            ))
          }
        </div>
        <div className="home-customerstory-button-container">
          <Link  to="/kundenmeinungen">
            <FaLongArrowAltRight/>mehr Kundenmeinungen
          </Link>
        </div>
      </div>

    )
  }
}