import React from 'react'
import "./Home.sass"
import { ContentLayout } from '../../components/Layout/ContentLayout';
import { graphql, Link } from 'gatsby';
import { ICustomerStory, IGatsbyLocation } from '../../interfaces';
import { HomeCustomerStory } from '../../components/CustomerStory/HomeCustomerStory';
import { FaChalkboardTeacher, FaChartBar, FaShippingFast, FaPhone } from 'react-icons/fa';
import { IHeaderProps } from '../../components/Layout/Header';

export class Home extends React.Component<{stories: ICustomerStory[], displayedStories?: number, location: IGatsbyLocation}>
{
  constructor(props)
  {
    super(props);
  }
  render()
  {
    let stories: ICustomerStory[] = this.props.stories;
    let header: IHeaderProps = {
      description: "Artikel, Tipps&Tricks, Online Kurse zu Excel Power Pivot, Power BI und Power Query | Trainings, Workshops und Implementierungsunterstützung durch Macher",
      title: "Home | Powerpivot Insights | Durchblick durch Daten",
      type: "website",
      url: "/"
    }

    let homepageStories = {
      hypo: stories.find(s => s.fields.story.company.toLowerCase().indexOf("hypo") >= 0),
      endress: stories.find(s => s.fields.story.company.toLowerCase().indexOf("endress") >= 0),
      flexsec: stories.find(s => s.fields.story.company.toLowerCase().indexOf("flex") >= 0),
      ruebezahl: stories.find(s => s.fields.story.company.toLowerCase().indexOf("rübezahl") >= 0),
    }

    return (
      <ContentLayout header={header}>
        <div className="container-nosidebar">
          <div className="section">
            <h1>Power Pivot Insights – der BI-Blog für den Durchblick durch Daten</h1>
            <p>
              Hier teilen wir mit dir unsere Leidenschaft für Datenauswertungen in vielen Artikeln zu Power BI, Excel Power Pivot und Power Query.
            </p>
          </div>
          <div className="section">
            <HomeCustomerStory stories={[
              homepageStories.endress,
              {
                childImageSharp: {
                  fixed: homepageStories.hypo.childImageSharp.fixed
                },
                fields: {
                  story: {
                    ...homepageStories.hypo.fields.story,
                    text:  "Excel Power Pivot ist für uns ein wichtiges Werkzeug in der Risikosteuerung, da wir große Datenmengen effizient analysieren müssen. In der kompetenten Schulung von Herrn Gubbels bekamen wir einen umfassenden Einblick [...].",
                  }
                }
              },
              // stories.find(s => s.fields.story.company.toLowerCase().indexOf("hypo") >= 0),
            ]}>
            </HomeCustomerStory>
          </div>
          <div className="section">
            <h1>Artikel für Neueinsteiger</h1>
            <ul>
              <li>
              <Link to="/quickstart-eine-erste-auswertung-mit-power-pivot">Das Quickstart Tutorial</Link>
              </li>
              <li>
              <Link to="/was-ist-power-pivot">Was ist Power Pivot?</Link>
              </li>
              <li>
              <Link to="/was-ist-power-bi">Was ist Power BI?</Link>
              </li>
              <li>
              <Link to="/was-ist-power-query">Was ist Power Query?</Link>
              </li>
              <li>
              <Link to="/power-pivot-power-bi-oder-power-query">Power Pivot, Power BI oder Power Query?</Link>
              </li>
            </ul>
            <p>
              Viele weitere Artikel findet ihr in unseren Blogposts zu den Themen<span> </span>
              <Link to="/category/power-bi">Power BI</Link><span>, </span>
              <Link to="/category/power-pivot">Power Pivot</Link><span>, </span>
              <Link to="/category/power-query">Power Query</Link>.
            </p>
          </div>
          <div className="section">
            <HomeCustomerStory stories={[
              homepageStories.flexsec,
              homepageStories.ruebezahl
            ]}>
            </HomeCustomerStory>
          </div>
          <div className="section">
            <h1>Unsere Angebote</h1>
            <div className="columns">
              <div className="column homepage-offer">
                <Link to="/trainings">
                  <div>
                    <FaChalkboardTeacher/>
                  </div>
                  <div>
                    Trainings
                  </div>
                </Link>
              </div>
              <div className="column">
              <div className="column homepage-offer">
                <Link to="/trainings">
                  <div>
                    <FaChartBar/>
                  </div>
                  <div>
                  Datenanalysen
                  </div>
                </Link>
              </div>
              </div>
              <div className="column">
              <div className="column homepage-offer">
                <Link to="/sofort-service"> {/*TODO: link*/}
                  <div>
                    <FaShippingFast/>
                  </div>
                  <div>
                    Sofort Service
                  </div>
                </Link>
              </div>
              </div>
            </div>
          </div>
          <div className="section">
            <div style={{textAlign: "center", fontSize: "32pt"}}>
              <a href="tel:+49(0)711/2172878-0">
                <span className="text-primary"><FaPhone/></span> 
                <span className="text-black" style={{fontWeight: "bold"}}> +49 (0) 711/2172878-0 </span>
              </a>
            </div>
          </div>

        </div>
      </ContentLayout>
    )
  }
}


const Page = ({data, pageContext, location}) => {
  return (
    <Home stories={data.stories.edges.map(e => e.node)} location={location}></Home>
  );
};
export default Page;

export const pageQuery = graphql`
  {
    ...customerStories
  }
`;
